import React from "react";
import { Link } from "react-router-dom"
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import InputGroup from 'react-bootstrap/InputGroup';
import Form from 'react-bootstrap/Form';
import Pagination from 'react-bootstrap/Pagination';

import ProvideTheme from "./ProvideTheme";
import ViewJobs from "../job/ViewJobs";

function Home() {

    return (
        <ProvideTheme>
            <Container>
                <Row>
                    <Col className="text-center">
                        {/* <AuthButton /> */}
                        <div className="jumbotron text-center">
                            <h2 >Are You Having Software Talent Hiring Issues Which Keep You up at Night? We Can Help!</h2>
                            <p>We help software talents and recruiters to connect with one another</p>
                            <p><Link to={"/recruiter/auth/signin"} className="btn btn-secondary btn-sm m-2">Talent</Link><Link to={"/recruiter/auth/signin"} className="btn btn-success btn-sm m-2">Employer</Link></p>
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col className="d-flex justify-content-around">
                        <InputGroup className="mb-3 m-2">
                            <Form.Control
                                placeholder="Search for jobs"
                                aria-label="Search for jobs"
                                aria-describedby="basic-addon2"
                            />
                            <Button variant="outline-secondary" id="button-addon2">
                                Search
                            </Button>
                        </InputGroup>

                    </Col>
                </Row>
                <ViewJobs />
                <Row>
                    <Col>
                        {
                            (() => {
                                let active = 2;
                                let items = [];
                                for (let number = 1; number <= 4; number++) {
                                    items.push(
                                        <Pagination.Item key={number + "page"} active={number === active}>
                                            {number}
                                        </Pagination.Item>,
                                    );
                                }
                                return <Pagination >{items}</Pagination>;
                            })()
                        }
                    </Col>
                </Row>
            </Container>
        </ProvideTheme>
    )
}

export default Home;