import React from "react";
import { ThemeProvider } from "react-bootstrap";

function ProvideTheme({children}) {
    return (
    <ThemeProvider
    breakpoints={['xxxl', 'xxl', 'xl', 'lg', 'md', 'sm', 'xs', 'xxs']}
    minBreakpoint="xxs"
    > {children} </ThemeProvider>
    )
}
export default ProvideTheme;